/* 全局样式 */
* {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC", "Helvetica Neue", STHeiti, "Microsoft Yahei", Tahoma, Simsun, sans-serif;
  background-color: #fff;
  color: #333;
  font-size: .28rem;
}
body {
  overflow-x: hidden;
}
ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}
a,
a:focus,
a:hover {
  cursor: pointer;
  color: inherit;
  text-decoration: none;
  outline: none;
}
input,
select {
  outline: none;
  border: none;
}
button {
  background: transparent;
  border: none;
  outline: none;
}
.clearfix {
  display: block;
  zoom: 1;
}
.clearfix::after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
  font-size: 0;
}
div:focus {
  outline: none;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
/* 文本截断 */
.lineText,
.lineText2,
.lineText3 {
  overflow: hidden;
  text-overflow: ellipsis;
}
.lineText2,
.lineText3 {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  box-orient: vertical;
}
.lineText {
  white-space: nowrap;
}
.lineText2 {
  -webkit-line-clamp: 2;
}
.lineText3 {
  -webkit-line-clamp: 3;
}



